.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 9vh;
  color: #fff;
  display: flex;
  min-height: 0vh;
  
  overflow: hidden;
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out; /* Transitions for sliding and background color animations */
}

.transparent {
  background-color: transparent; /* Make the background transparent */
}

.black-bg {
  background-color: #000; /* Set the background color to black */
}

.slide-up {
  transform: translateY(-100%); /* Slide the navbar up and off the screen */
}
.visible {
  transform: translateY(0%); /* Slide the navbar down and on the screen */
}
.navbar-content {
  display: flex;
  justify-content: space-between; /* Align logo and button to opposite ends */
  align-items: center;
  padding: 0 10px; /* Adjust padding as needed */
  width: 100%;
}

.logo-container {
  display: flex;
  align-items: center;
  margin: 0;
}

.logo {
  height: 50px;
  width: auto;
  margin:0;
}

.logo-text {
  margin: 0;
  font-size: 2.5rem;
}

.menu {
  display: none;
}
.desktop-menu {
  font-size: 1.5rem;
  display: flex;
  align-items: center; /* Vertically align menu items */
  gap: 20px; /* Add spacing between menu items */
  padding-right: 20px;
}

.desktop-menu span {
  position: relative;
}

.desktop-menu span.signup {
  display: inline-block;
  padding: 6px 12px; /* Adjust padding as needed */
  border: 1px solid white; /* White outline */
  border-radius: 4px; /* Rounded corners */
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.desktop-menu span.signup:hover {
  border: 1px solid white;
  background-color: white;
  color: black;
}
.mobile-menu {
  display: none;
}
@media only screen and (max-width: 500px) and (max-height: 900px) {
    .navbar {
      height: 8vh;
    }
    .logo-container {
      display: flex;
      align-items: center;
      margin: 0;
    }
    
    .logo {
      height: 50px;
      width: auto;
      margin:0;
    }
    
    .logo-text {
      margin: 0;
      font-size: 1.5rem;
    }
    
    .menu {
      background: none;
      border: none;
      color: white;
      cursor: pointer;
      font-size: 1rem;
      display: contents;
    }
    .desktop-menu {
      display: none; /* Hide desktop menu by default on mobile */
    }
    .menu-open {
      overflow: hidden; /* Prevent scrolling when menu is open */
    }
    
    .mobile-menu {
      position: fixed;
      bottom: -100%; /* Initially off-screen */
      left: 0;
      width: 100%;
      height: 100%;
      background-color: white;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      transition: transform 0.3s ease-in-out;
      z-index: 2;
    }
    
    .mobile-menu.open {
      transform: translateY(-100%); /* Slide up into view */
    }
    
    .menu-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px; /* Add spacing between menu items */
      padding: 20px; /* Add padding around the buttons */
    }
    
    .menu-content button {
      background: none;
      border: none; /* Remove the default button border */
      padding: 10px 20px; /* Add padding to the buttons */
      font-size: 1.5rem;
      cursor: pointer;
      transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
    }
    
    .menu-content button:hover {
      background-color: black;
      color: white;
    }
  }